@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  text-align: left;
  background-color: #f2f2f2;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
@media (max-width: 767px) {
  .page-header {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 79px;
    background-color: #ffffff;
  }
  .page-header__logo-wrapper {
    position: relative;
    min-height: 79px;
    background-color: #ffffff;
  }
  .page-header__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    display: block;
    width: 86px;
    height: 35px;
    margin-left: -43px;
    margin-top: -17.5px;
  }
  .page-header__logo-image {
    width: 86px;
    height: 35px;
  }
}
@media (min-width: 768px) {
  .page-header {
    background-color: #ffffff;
  }
  .page-header__logo-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 320px;
    min-height: 88px;
  }
  .page-header__logo {
    display: block;
    padding-top: 22px;
    padding-left: 50px;
  }
  .page-header__logo-image {
    width: 112px;
    height: 46px;
  }
  .page-header__wrapper {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 1150px) {
  .page-header__logo-wrapper {
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 1;
    display: block;
    width: 149px;
    height: 72px;
    margin-left: -74.5px;
  }
  .page-header__logo {
    padding: 0;
    padding-top: 9px;
    width: 149px;
    height: 72px;
  }
  .page-header__logo-image {
    width: 149px;
    height: 72px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    width: 320px;
  }
  .main-nav__toggle {
    position: absolute;
    padding: 0;
    top: 20px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 40px;
    height: 40px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .main-nav__toggle:hover,
  .main-nav__toggle:focus {
    background-color: transparent;
    opacity: 0.6;
  }
  .main-nav__toggle:active {
    background-color: transparent;
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__toggle::before,
  .main-nav--closed .main-nav__toggle:active::before {
    content: "";
    position: absolute;
    top: 9px;
    right: 10px;
    width: 20px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 19px;
    width: 2px;
    height: 25px;
    background-color: #000000;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: #ffffff;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav__main-list,
  .main-nav__nav-wrapper {
    margin: 0;
    padding: 0;
    list-style: none;
    background-color: #ffffff;
  }
  .main-nav__new-list {
    display: none;
  }
  .main-nav__link-delivery {
    display: none;
  }
  .main-nav--closed .main-nav__main-list,
  .main-nav--closed .main-nav__nav-wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
}
.main-nav__item-link {
  display: block;
  min-height: 79px;
  padding-top: 27px;
  padding-bottom: 28px;
  padding-left: 76px;
  padding-right: 20px;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
}
.main-nav__item-link[href]:hover,
.main-nav__item-link[href]:focus {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__item-link[href]:hover .main-nav__icon-cart,
.main-nav__item-link[href]:focus .main-nav__icon-cart {
  fill: #63d1bb;
  opacity: 1;
}
.main-nav__item-link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__item-link[href]:active .main-nav__icon-cart {
  fill: #63d1bb;
  opacity: 0.3;
}
.main-nav__item-link--cart {
  padding-left: 31px;
  box-shadow: 0px 1px 0 0 #ececec, inset 0px 1px 0 0 #ececec;
}
.main-nav__search {
  position: relative;
}
.main-nav__search-input {
  width: 320px;
  min-height: 79px;
  border: none;
}
.main-nav__search-input:hover::-webkit-input-placeholder,
.main-nav__search-input:focus::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__search-input:hover::-moz-placeholder,
.main-nav__search-input:focus::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__search-input:hover:-ms-input-placeholder,
.main-nav__search-input:focus:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__search-input:hover:-moz-placeholder,
.main-nav__search-input:focus:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__search-input:hover[placeholder],
.main-nav__search-input:focus[placeholder] {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__search-input:active::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__search-input:active::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__search-input:active:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__search-input:active:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__search-input:active[placeholder] {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__search-input:hover + .main-nav__button .main-nav__icon-search,
.main-nav__search-input:focus + .main-nav__button .main-nav__icon-search {
  fill: #63d1bb;
  opacity: 1;
}
.main-nav__search-input:active + .main-nav__button .main-nav__icon-search {
  fill: #63d1bb;
  opacity: 0.3;
}
.main-nav__search-input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #000000;
}
.main-nav__search-input::-moz-placeholder {
  /* Firefox 19+ */
  color: #000000;
}
.main-nav__search-input:-ms-input-placeholder {
  /* IE 10+ */
  color: #000000;
}
.main-nav__search-input:-moz-placeholder {
  /* Firefox 18- */
  color: #000000;
}
.main-nav__search-input[placeholder] {
  min-height: 79px;
  padding-top: 27px;
  padding-bottom: 28px;
  padding-left: 76px;
  padding-right: 20px;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  text-align: left;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
}
.main-nav__button {
  position: absolute;
  z-index: 2;
  top: 32px;
  left: 34px;
  background-color: transparent;
  border: none;
}
.main-nav__icon-search {
  fill: #000000;
  opacity: 0.2;
}
.main-nav__icon-cart {
  margin-right: 25px;
  fill: #000000;
  opacity: 0.2;
}
